
.about-container{
    margin-top: 4rem;
}
.app__about {
  flex: 1;
  width: 100%;
  flex-direction: column;
  
}

.app__profiles {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-top: 2rem;
}

.app__profile-item {
  width: 190px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  margin: 2rem;

  img {
    width: 100%;
    height: 170px;
    border-radius: 15px;
    object-fit: cover;
  }

  @media screen and (min-width: 2000px) {
    width: 370px;
    margin: 2rem 4rem;

    img {
      height: 320px;
    }
  }
}

@import url('https://fonts.googleapis.com/css2?family=Alkalami&family=Lato&family=Montserrat&family=Roboto+Condensed&display=swap');



.about-parent{
    margin-top: 4rem;
    margin-right: 16rem;
    margin-bottom: 5rem;
    display: grid;
   grid-template-columns: 35% 50%;
   gap: 15%;
   
    
}

.about-picture{
    display:flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 1px 0px 0px black;
    background-color: antiquewhite;
    height: 510px;
    width: 410px;
   margin-left: 10rem;
    border-radius: 2rem;
    
}

.about-picture-background{
    height:92%;
    width: 92%;
    background-image: url("../../assets/me-about.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    transform: rotate(0deg);
    border-radius: 2rem;

}

.about-picture-background:hover{
    transform: rotate(0) ;
    transition: 1s ease-out;
}

.about-content{
    margin-left: 4rem;
}

.about-cards{
    
    
    display: flex;
    gap: 20px;
    
    
}
.about-card{
    display:flex-end;
    text-align:center;
    padding-top: 20px;
    line-height: 1px;
    box-shadow: 0 1px 0px 0px black;
    background-color: rgb(204, 189, 189);
    height: 110px;
    width: 150px;
   
    border-radius: 1rem;

}
.about-section{
    
    margin-bottom: 1rem;
    font-family: 'Lato', sans-serif  ;
    color: #080806;
    line-height: 29px;
    letter-spacing: 1px;
    font-size: 18px;
    width: 88vh; 

}

.about-me{
  font-family: 'Alkalami', serif;
  font-size: 2rem;
  text-align: center;
  font-family: "Lobster";
 text-decoration: underline;
 
 margin-left: 45%;
}

.skills1{
    padding-top: 15px;
    display: flex;
    gap: 5rem;
   font-size: 17px;
   
}
.skills2{
    margin-left: 3px;
    display: flex;
    gap: 4.4rem;
    font-size: 17px;
}

.mogo{
    margin-left: 54px;
}
.node{
    margin-left: 11px;
}

