.btn-primary{
    
    transition: all .5s ease;
    color: #000;
    border: 3px solid rgb(41, 115, 161);
    font-family: 'Alkalami', serif;
    text-transform: uppercase;
    text-align: center;
    padding: 5px 19px 0px;
    font-size: 12px;
    background-color : transparent;
    letter-spacing: 3px;
    outline: none;
    border-radius: 4px;
    margin-right: 60px;
    text-decoration: none;
}
.btn-primary:hover{
    color: #001F3F;
    background-color: #fff;
}



@media screen and (max-width: 1110px) {
    .about-picture{
        margin: 0 0px 0 80px;
    }

   
    
}

@media screen and (max-width: 910px) {
    .about-picture{
        height: 320px;
        width: 320px;
        margin: 0 0 0 4px;
    }
}

@media screen and (max-width: 810px) {
    .about-picture{
        margin: 0 !important;
    }

    .about-parent{
        flex-direction: column-reverse;
        margin: 40px 0 0 0;
    }

    
   
}

@media screen and (max-width: 375px) {
    .about-picture{
        height: 275px;
        width: 275px;
        margin-top: -90px;
    }

   
}



