@import url('https://fonts.googleapis.com/css2?family=Alkalami&family=Lato&family=Montserrat&family=Roboto+Condensed&display=swap');
.background3{
    display: flex;
    background:#f8f6f1;
    
    height: 800px;
    align-items: center;
    text-align: center;
    justify-content: center;
    background-image: url("../../assets/background7.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    overflow-x: hidden;
    
   
   
}
.intro-container{
   
   margin-top: 8rem; 
   line-height: 55px;
   
   height: 30rem;
   width: 50rem;
   
}
.intro-section2{
    font-size: 40px;
    font-family: 'Alkalami', serif;
    padding-top: 10px;
    letter-spacing: 3px;
    color: #343e10;

}
.intro-section1{
    
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 4px;
    color: #000;
    
}

.intro-section4{
    font-size: 20px;
    font-family: 'Lato', sans-serif;;
    line-height: 35px;
    letter-spacing: 2px;
    color: #301d0a;
}

.intro-section3{
    font-size: 30px;
    font-family: 'Alkalami', serif;
    letter-spacing: 3px;
    color: #253106;
    
}

.intro-btn1 {
    transition: all .5s ease;
    color: rgb(241, 203, 203);
    border: 3px solid rgb(41, 115, 161);
    font-family: 'Alkalami', serif;
    text-transform: uppercase;
    text-align: center;
    padding: 5px 19px 0px;
    font-size: 12px;
    background-color : transparent;
    letter-spacing: 3px;
    outline: none;
    border-radius: 4px;
    margin-right: 60px;
}

.intro-buttons{
    padding-top: 17px;
}
.intro-btn1:hover {
    color: #001F3F;
    background-color: #fff;
}

.intro-btn2 {
    transition: all .5s ease;
    letter-spacing: 3px;
    color: rgb(241, 203, 203);
    
    border: 3px solid  rgb(41, 115, 161);
    font-family: 'Alkalami', serif;
    text-transform: uppercase;
    text-align: center;
    background-color :transparent;
    
    padding: 5px 19px 0px;
    font-size: 12px;
    outline: none;
    border-radius: 4px;
    font-size: 12px;
    margin-left: 50px;
}
.intro-btn2:hover {
    color: #001F3F;
    background-color: #fff;
}
 
 
 
 